  --primary-color: #ff5722;
  --secondary-color: #fff;
  --dark-color: #333;
  --light-gray: #f7f7f7;
}

/* Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
  line-height: 1.6;
}

a, button, input, textarea{
  display: inline-block;
  min-width: 44px;
  min-height: 44px;
}

/* Skip to Content */
.skip-link {
  position: absolute;
  top: -45px;
  left: 0;
  background: #000;
  color: #fff;
  padding: 8px 16px;
  z-index: 100;
}

.skip-link:focus {
  top: 0;
}

/* Ukuran Logo */
.logo {
  width: 50px;
  height: auto;
  margin-right: 10px;
}

/* App Bar */
.app-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--primary-color);
  color: var(--secondary-color);
  padding: 10px 20px;
  position: sticky;
  top: 0;
  z-index: 99;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
}

.app-bar .brand {
  display: flex;
  align-items: center;
}

.app-bar .nav-menu {
  display: flex;
  gap: 20px;
}

.app-bar .nav-menu.open {
  display: flex;
  opacity: 1;
  transform: translateY(0);
  transition: all 0.3s ease;
}

.app-bar .nav-menu ul {
  display: flex;
}

.app-bar .nav-menu li {
  list-style: none;
}

.app-bar .nav-menu a {
  color: #fff;
  text-decoration: none;
  padding: 8px 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 44px;
  min-height: 44px;
}

.app-bar .hamburger {
  background: none;
  border: none;
  font-size: 24px;
  color: var(--secondary-color);
  cursor: pointer;
  display: none;
  min-width: 44px;
  min-height: 44px;
}

/* Hero */
.hero {
  text-align: center;
  background-color: #f7f7f7;
  width: 100%;
}

.hero img {
  width: 100%;
  max-width: 1200px;
  min-width: 100%;
  height: auto;
}

.hero h2 {
  margin-top: 20px;
  color: #333;
}

/* Restaurant List */
/* .restaurant-list {
  padding: 20px;
  background-color: #fff;
}

.restaurant-list h2 {
  margin-bottom: 20px;
}

.restaurant-list #restaurants {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
}

.restaurant-item {
  border: 1px solid #ddd;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.restaurant-item img {
  width: 100%;
  height: 150px;
  object-fit: cover;
}

.restaurant-item h3 {
  padding: 10px;
  color: #ff5722;
}

.restaurant-item p {
  padding: 10px;
  color: #333;
} */

.restaurantCatalog, .restaurants {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 16px;
  padding: 16px;
}

.restaurant-item {
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  transition: transform 0.2s ease-in-out;
}

.restaurant-item:hover {
  transform: scale(1.05);
}

.restaurant-item__image {
  width: 100%;
  /* height: 150px; */
  height: auto;
  max-height: 300px;
  object-fit: cover;
}

.restaurant-item__content {
  padding: 16px;
}

.restaurant-item__title {
  font-size: 1.25rem;
  margin: 0 0 8px;
}

.restaurant-item__info {
  font-size: 0.875rem;
  color: #666;
}

.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #db0000;
  
  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;
  
  font-size: 18px;
  color: white;
  cursor: pointer;
}

footer {
  text-align: center;
  padding: 20px;
  background-color: #333;
  color: #fff;
}

.nav-menu.open {
  display: flex;
  opacity: 1;
  transform: translateY(0);
}

@media screen and (max-width: 768px) {
  .app-bar .nav-menu {
    display: hidden;
    background-color: #ff5722;
    position: absolute;
    top: 60px;
    right: 0;
    width: 200px;
    padding: 10px;
    border-radius: 0 0 8px 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    opacity: 0;
    transform: translateY(-20px);
    transition: all 0.3s ease;
  }

  .app-bar .nav-menu ul {
    display: flex;
    flex-direction: column;
  }

  .app-bar .hamburger {
    display: block;
  }

  .nav-menu.open {
    display: visible;
    opacity: 1;
    transform: translateY(0);
  }
}

/* Hero on larger screens (min-width 1200px) */
@media screen and (min-width: 1200px) {
  .hero img {
    width: 100%;
    max-width: 1200px;
    min-width: 1000px;
  }
}
